import React from "react";

function Textarea({
  placeholder,
  name,
  value = "",
  onValueChange = () => {},
  required,
}) {
  return (
    <div className="input-container">
      <textarea
        placeholder={placeholder}
        className="input"
        value={value}
        name={name}
        required={required}
        onChange={(e) => {
          onValueChange(e.target.value);
        }}
        rows={5}
        style={{ width: "100%" }}
      />
      <i
        className={`fa fa-close ${value.length > 0 ? "active" : ""} `}
        onClick={() => onValueChange("")}
      />
    </div>
  );
}

export default Textarea;
