import React from "react";
import { STYLES } from "../assets/style";

function Modal({
  isActive = false,
  style = {},
  onModalOutSideClick = () => {},
  children,
  animationType = "zoomIn",
}) {
  return (
    <div
      className={`modal-overlay ${isActive ? "active" : ""} `}
      onClick={onModalOutSideClick}
    >
      <div className={`modal-overlay-main`} onClick={onModalOutSideClick}>
        <div
          className={`modal ${STYLES[animationType]}`}
          style={style}
          onClick={(e) => {
            e.preventDefault();
            e.stopPropagation();
          }}
        >
          {children}
        </div>
      </div>
    </div>
  );
}

export default Modal;
