import React, { useState } from "react";
import Modal from "../components/Modal";
import { STYLES } from "../assets/style";

function Projects() {
  const [projectType, setProjectType] = useState("All");
  const [isActive, setIsActive] = useState(false);
  const [activeData, setActiveData] = useState(null);
  const [activeIndexStyle, setActiveIndexStyle] = useState({
    index: 0,
    next: true,
    prev: false,
  });

  const typesContent = {
    "HTML/CSS": [
      {
        imageUrl: "/img/signup/img1.jpg",
        title: "UI HTML-CSS signup form ",
        description: "HTML / CSS",
        content: {
          images: [
            { imageUrl: "/img/signup/img1.jpg" },
            { imageUrl: "/img/signup/img2.jpg" },
          ],
          title: "UI HTML-CSS signup form",
          subTitle: "UI design from the given img",
          description:
            "UI HTML-CSS signup form as given in the user design image",
          url: "https://siddaiahm.github.io/html-css-signup-form/",
        },
      },
      {
        imageUrl: "/img/fourcards/img1.jpg",
        title: "UI HTML-CSS four cards design",
        description: "HTML / CSS",
        content: {
          images: [{ imageUrl: "/img/fourcards/img1.jpg" }],
          title: "UI HTML-CSS four cards design",
          subTitle: "UI design from the given img",
          description:
            "UI HTML-CSS four cards as given in the user design image",
          url: "https://siddaiahm.github.io/html-css-four-cards/",
        },
      },
      {
        imageUrl: "/img/pricing/img1.jpg",
        title: "UI HTML-CSS pricing content design",
        description: "HTML / CSS",
        content: {
          images: [
            { imageUrl: "/img/pricing/img1.jpg" },
            { imageUrl: "/img/pricing/img2.jpg" },
            { imageUrl: "/img/pricing/img3.jpg" },
          ],
          title: "UI HTML-CSS pricing content design",
          subTitle: "UI design from the given img",
          description:
            "UI HTML-CSS pricing content as given in the user design image",
          url: "https://siddaiahm.github.io/html-css-pricing/",
        },
      },
    ],

    "React Native": [
      {
        imageUrl: "/img/styleon/bg.jpg",
        title: "StyleOn Hybrid Mobile App",
        description: "React Native / Node js",
        content: {
          images: [
            { imageUrl: "/img/styleon/img1.jpg" },
            { imageUrl: "/img/styleon/img2.jpg" },
            { imageUrl: "/img/styleon/img3.jpg" },
          ],
          title: "StyleOn Hybrid Mobile App",
          subTitle: "Is a e-commerce mobile application",
          description:
            "Online Shopping Mobile Application for Fashion & Lifestyle in India. India's Fashion Expert brings you a variety of Clothings.",
        },
      },
      {
        imageUrl: "/img/sidebar/img1.jpg",
        title: "App with animated side bar",
        description: "React Native",
        content: {
          images: [
            { imageUrl: "/img/sidebar/img1.jpg" },
            { imageUrl: "/img/sidebar/vd1.mp4" },
          ],
          title: "App with animated side bar",
          subTitle: "With an animated side bar",
          description:
            "Mobile Application with animated side bar",
        },
      },
    ],
    "React js": [
      {
        imageUrl: "/img/udacity-clone/img1.jpg",
        title: "Udacity Clone Web App",
        description: "React js/Node js",
        content: {
          images: [
            { imageUrl: "/img/udacity-clone/img2.jpg" },
            { imageUrl: "/img/udacity-clone/img3.png" },
            { imageUrl: "/img/udacity-clone/img4.png" },
            { imageUrl: "/img/udacity-clone/img5.jpg" },
          ],
          title: "Udacity Clone Web App",
          subTitle: "Is the Clone of Udacity Web App",
          description:
            "Udacity is an online learning platform for those interested in tech-related subjects such as data analysis, software engineering, artificial intelligence and web development.\n Courses are interactive and visual, and include quizzes, short videos and projects that students can add to their portfolios.",
          url: "https://udacity-clone.netlify.app/",
        },
      },
    ],
  };

  typesContent["All"] = Object.values(typesContent).reduce(
    (acc, cur) => [...cur, ...acc],
    []
  );

  const types = Object.keys(typesContent)
    .filter((type) => typesContent[type].length > 0)
    .reverse();

  const handleLearnMoreClick = (data) => {
    setActiveIndexStyle({
      index: 0,
      next: true,
      prev: false,
    });
    setActiveData(data);
    setIsActive(true);
  };

  const handleRightClick = () => {
    setActiveIndexStyle({
      index: activeIndexStyle.index + 1,
      prev: true,
      next: activeIndexStyle.index < activeData.images.length - 2,
    });
  };

  const handleLeftClick = () => {
    setActiveIndexStyle({
      index: activeIndexStyle.index - 1,
      prev: activeIndexStyle.index > 1,
      next: true,
    });
  };

  const handleViewSite = (url) => {
    window.open(url, "_blank");
  };

  return (
    <div className="container">
      <div className={"title " + STYLES.bounceInLeft}>
        Projects
        <div />
      </div>
      <div className={"content-container " + STYLES.fadeIn}>
        <div className="projects-content-contaner">
          <div className="types-contaner">
            {types.map((type, index) => (
              <button
                key={index}
                className={`type ${type === projectType ? "active" : ""}`}
                onClick={() => setProjectType(type)}
              >
                {type}
              </button>
            ))}
          </div>
          <div className="types-content">
            {typesContent[projectType].map(
              ({ imageUrl, title, description, content }, index) => (
                <div key={index} className="type-content">
                  <img src={imageUrl} style={{objectFit: "contain"}} />
                  <div className="text-desc">
                    <div className={STYLES.slideInDown}>
                      <div className="t">{title}</div>
                      <div className="d">{description}</div>
                    </div>
                    <button
                      className={STYLES.slideInUp}
                      onClick={() => handleLearnMoreClick(content)}
                    >
                      LEARN MORE
                    </button>
                  </div>
                </div>
              )
            )}
          </div>
        </div>
      </div>
      <Modal
        isActive={isActive}
        onModalOutSideClick={() => {
          setIsActive(false);
        }}
      >
        {activeData ? (
          <>
            <div className="p-modal-img-container">
              {activeData.images[activeIndexStyle.index].imageUrl.includes(".mp4")?
              <div style={{height:"40vh"}} >
              <video width="100%" height="100%" autoPlay loop>
                <source src={activeData.images[activeIndexStyle.index].imageUrl} type="video/mp4"/>
              </video>
              </div>
                :<img src={activeData.images[activeIndexStyle.index].imageUrl} style={{height:"40vh",objectFit: "contain"}}/>
              }
              {activeData.images.length > 1 ? (
                <div className="p-modal-img-slider">
                  {activeIndexStyle.prev ? (
                    <i
                      className="fa fa-chevron-left"
                      style={{ left: 0 }}
                      onClick={handleLeftClick}
                    />
                  ) : null}
                  <div style={{ display: "flex", flexDirection: "row" }}>
                    {activeData.images.map((_, index) => (
                      <div
                        key={index + "d"}
                        style={{
                          height: 10,
                          width: 10,
                          margin: 5,
                          borderRadius: "50%",
                          backgroundColor:
                            activeIndexStyle.index === index
                              ? "black"
                              : "lightgrey",
                        }}
                      />
                    ))}
                  </div>
                  {activeIndexStyle.next ? (
                    <i
                      className="fa fa-chevron-right"
                      style={{ right: 0 }}
                      onClick={handleRightClick}
                    />
                  ) : null}
                </div>
              ) : null}
            </div>
            <div className="p-img-content">
              <h2>{activeData.title}</h2>
              <p style={{ color: "#00000080", paddingLeft: 10 }}>
                {activeData.subTitle}
              </p>
              <p style={{ fontSize: 14, padding: 10 }}>
                {activeData.description}
              </p>
              {activeData.url ? (
                <a
                  href={activeData.url}
                  onClick={() => handleViewSite(activeData.url)}
                >
                  View Site
                </a>
              ) : null}
            </div>
          </>
        ) : null}
      </Modal>
    </div>
  );
}

export default Projects;
