import React, { useState } from "react";
import { STYLES } from "../assets/style";
import Input from "../components/Input";
import Textarea from "../components/Textarea";
import emailjs from "emailjs-com";
import Alert from "../components/Alert";

function Contact() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [messageSent, setMessageSent] = useState(false);
  const [loading, setLoading] = useState(false);

  const sendMail = (e) => {
    setLoading(true);
    e.preventDefault();
    emailjs
      .sendForm(
        "service_2nr2cra",
        "template_w9mgxu9",
        e.target,
        "user_QlBH23YBg1yusRFpldJ3e"
      )
      .then((message) => {
        setMessageSent(true);
        setLoading(false);
      })
      .catch((message) => {
        console.log(JSON.stringify(message) + "e");
        setLoading(false);
      });
  };

  return (
    <div className="container">
      <div className={"title " + STYLES.bounceInLeft}>
        Contact
        <div />
      </div>
      <div className="content-container">
        <div className="contact-content-container">
          <div className="social">
            <a
              href="https://www.linkedin.com/in/siddaiahm"
              target="_blank"
              className="fa fa-linkedin"
              style={{ color: "#046af0" }}
            />
            <a
              href="https://github.com/siddaiahm"
              target="_blank"
              className="fa fa-github"
            />
            <a
              href="https://www.instagram.com/_the_fascinating_mortal_/"
              target="_blank"
            >
              <img src="/img/instagram.svg" style={{ width: 30, height: 30 }} />
            </a>
          </div>
          <div className="form">
            <div className={"q " + STYLES.zoomIn}>
              Have a question or want to work together?
            </div>
            <form className="contact-form" onSubmit={sendMail}>
              <Input
                key="name"
                name="name"
                required
                placeholder="Your Name"
                value={name}
                onValueChange={(value) => setName(value)}
              />
              <Input
                key="email"
                name="email"
                required
                placeholder="Your Email"
                value={email}
                onValueChange={(value) => setEmail(value)}
              />
              <Textarea
                key="mesg"
                name="message"
                required
                placeholder="Your Message"
                value={message}
                onValueChange={(value) => setMessage(value)}
              />
              <div className="form-btn">
                <button type="submit" disabled={loading}>
                  {loading ? "Sending..." : "Send"}
                </button>
              </div>
            </form>
          </div>
        </div>
        <Alert isActive={messageSent}>
          <i className="fa fa-thumbs-up" />
          <div className="text">
            Tank you.
            <br /> I have recieved your message.
            <br />I will get back to you asap.
          </div>
          <button onClick={() => setMessageSent(false)}>Ok</button>
        </Alert>
      </div>
    </div>
  );
}

export default Contact;
